* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "open sans", sans-serif;
  font-size: 20px;
}

div.app {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	margin: 0 auto;
	color: #222;
	align-items: center;
	overflow: hidden;
}
main {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	flex-grow: 1;
	margin-bottom: 100px;
	width: 90%;
	max-width: 1200px;
	overflow: hidden;
}
.blog {
	width: 80%;
	justify-content: center;
	text-align: center;
}
h3 {
	text-transform: none;
	font-size: 28px;
}
article:nth-child(2) h3,
article:nth-child(3) h3 {
	font-size: 22px;
}
article p {
	font-size: 18px;
}
article h1 {
	font-size: 28px;
}
@media (max-width: 300px) {
	main {
		margin: 0 0 50px 20px;
	}
}

@media (min-width: 720px) {
	main {
		display: flex;
		justify-content: space-around;
		flex-direction: row;
		flex-grow: 1;
		width: 90%;
		max-width: 1200px;
	}
	.blog {
		width: 50%;
	}
}
@media (min-width: 1080px) {
	h3 {
		font-size: 30px;
	}
	article:nth-child(2) h3,
	article:nth-child(3) h3 {
		font-size: 24px;
	}
	article p {
		font-size: 20px;
	}
	article h1 {
		font-size: 30px;
	}
	.blog {
		text-align: left;
	}
}

header {
	flex-basis: 40vh;
	position: relative;
	overflow: hidden;
	width: 100vw;
}

header img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	min-height: 100%;
}
@media (max-width: 500px) and (orientation: portrait) {
	header img {
		height: 100%;
		width: auto;
	}
}
@media (max-width: 500px) and (orientation: landscape) {
	header img {
		top: -100px;
	}
}
@media (min-width: 720px) {
	header img {
		top: -100px;
	}
}

@media (min-width: 1080px) {
	header img {
		top: -300px;
	}
}

.main ul {
	list-style-type: none;
	background-color: white;
	border-right: none;
	border-top-left-radius: 30px;
	min-width: 130px;
}

nav.main a {
	display: block;
	text-decoration: none;
	color: black;
	text-transform: uppercase;
	font-weight: bold;
	transition: 0.2s;
	padding: 10px 20px;
}

nav.main a:hover {
	-webkit-transform: translateX(10px);
	        transform: translateX(10px);
	transition: 0.2s;
}

nav.main a.active {
	-webkit-transform: translateX(0);
	        transform: translateX(0);
	border-radius: 5px;
	transition: 0.3s;
}

nav.main li:nth-child(1) a.active {
	border-left-color: #55417b;
	color: #55417b;
}

nav.main li:nth-child(2) a.active {
	border-left-color: #0e384b;
	color: #0e384b;
}

nav.main li:nth-child(3) a.active {
	border-left-color: #193c1b;
	color: #193c1b;
}

nav.main li:nth-child(4) a.active {
	border-left-color: #380e0e;
	color: #380e0e;
}
.navWrapper i {
	font-size: 35px;
	transition: 0.5s;
	padding: 0 5px;
}

.navWrapper {
	background-color: white;
	display: flex;
	align-items: center;
	justify-content: space-between;
	position: fixed;
	min-width: 180px;
	max-width: 100vw;
	left: -150px;
	top: 38vh;
	height: auto;
	border-radius: 30px;
	background-color: #eee;
	box-shadow: 1px 1px 5px 0px rgba(66, 68, 90, 1);
	transition: 0.5s;
}
.navWrapper.show {
	left: 0;
}
.navWrapper i:nth-child(1).show {
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
}
.navWrapper i:nth-child(3).show {
	-webkit-transform: rotate(-180deg);
	        transform: rotate(-180deg);
}
.navButton:hover {
	cursor: pointer;
}

.navButton div {
	display: flex;
	flex-direction: column;
	align-items: center;
	color: #20123c;
}
.navButton p {
	-webkit-transform: rotate(-90deg);
	        transform: rotate(-90deg);
	padding: 20px 0;
	font-weight: 700;
	font-size: 15px;
}
@media (min-width: 720px) {
	.main ul {
		min-width: auto;
		margin: 20px;
	}
	nav.main a.active {
		border-left: 10px gray solid;
	}

	.navWrapper {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		position: relative;
		top: auto;
		left: auto;
		height: auto;
		background-color: transparent;
		border: none;
		min-width: 200px;
	}
	.navWrapper i:nth-child(n) {
		display: none;
	}
	.navWrapper p:nth-child(n) {
		display: none;
	}
}

article {
	color: #20123c;
	margin-top: 20px;
	display: flex;
	flex-direction: column;
}

article:nth-child(n) h3 {
	margin-bottom: 5px;
}

.products h2 {
	margin-bottom: 20px;
	display: inline-block;
	max-width: 300px;
}

.products ul {
	list-style: none;
}

.products li {
	margin-bottom: 20px;
	text-align: center;
}
.products a {
	display: inline-block;
	text-decoration: none;
	color: #20123c;
	padding: 2% 3%;
	transition: 0.5s;
	border-radius: 20px;
	border: 2px solid #20123c;
	background-color: #e7e2df;
	text-align: center;
	font-weight: 700;
}

.products a:hover {
	color: white;
	background-color: #4a326c;
	border-color: #bdb6c6;
}
.product {
	justify-content: center;
	color: #20123c;
}
.product h1 {
	margin-bottom: 20px;
}
.product p {
	margin-bottom: 20px;
}
.product a {
	display: inline-block;
}
.blog a {
	display: inline-block;
	height: 100%;
	max-width: 400px;
	text-decoration: none;
	color: #20123c;
	padding: 2% 7%;
	transition: 0.5s;
	border-radius: 20px;
	border: 2px solid #20123c;
	background-color: #e7e2df;
	align-items: center;
}

.blog a:hover {
	color: white;
	background-color: #4a326c;
	border-color: #bdb6c6;
}
@media (min-width: 1080px) {
	.products li {
		text-align: left;
	}
}

.formWrapper {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
}
.formWrapper div {
	display: flex;
	flex-direction: column;
	width: 100%;
	align-items: center;
}

.formWrapper textarea {
	border: 1px solid #20123c;
	border-radius: 5px;
	font-size: 16px;
	width: 100%;
	min-height: 200px;
	margin: 20px 0;
	line-height: 1.6;
	box-shadow: 1px 1px 5px 0px #20123c;
}

.formWrapper button {
	display: block;
	border: 2px solid #20123c;
	border-radius: 20px;
	padding: 10px 20px;
	cursor: pointer;
	font-size: 18px;
	font-weight: 700;
	transition: 0.2s;
	color: #20123c;
	background-image: linear-gradient(to right, #bdb6c6, white);
	transition: 1s;
}

.formWrapper button:hover {
	background-image: linear-gradient(to right, #20123c, #bdb6c6);
	color: white;
}

.formWrapper input {
	border: 1px solid #20123c;
	border-radius: 5px;
	height: 25px;
	margin-top: 10px;
	width: 100%;
	box-shadow: 1px 1px 5px 0px #20123c;
}

@media (min-width: 720px) {
	.formWrapper div {
		width: 80%;
	}
}
@media (min-width: 1080px) {
	.formWrapper button {
		padding: 10px 205x;
		font-size: 20px;
	}
}

footer {
	width: 100vw;
	background-color: #20123c;
	color: white;
	padding: 5px 0;
	text-align: center;
	box-shadow: 0 -1px 5px #20123c;
}

footer .footerContainer {
	display: flex;
	flex-direction: column;
	align-items: center;
}
footer .footerWrapper {
	display: flex;

	font-size: 12px;
	align-items: center;
}
footer .footerWrapper p {
	color: white;
	font-size: 14px;
	text-transform: none;
}
footer .footerWrapper i {
	margin: 0 5px;
}

footer p {
	text-transform: uppercase;
	font-size: 8px;
	color: #eeee;
}

